import {React, useState} from "react";
import "./App.css";
import { Canvas} from "react-three-fiber";
import Room from "./Components/models/room2.gltf";
import { useGLTFLoader, OrbitControls } from "drei";
import { BrowserRouter, Route, useHistory } from "react-router-dom";
import Modal from "./Components/Modal/Modal.js";
import {useSpring, animated} from 'react-spring';
// import PopUp from "./Components/Modal/PopUp.js"

//3d Models
import Introduction from "./Components/models/btnintroduction.gltf";
import Featured from "./Components/models/btnfeatured.gltf";
import Values from "./Components/models/btnvalues.gltf";
import Interests from "./Components/models/btninterests.gltf";
import Current from "./Components/models/btncurrent.gltf";
import Instagram from "./Components/models/linkinstagram.gltf";
import Youtube from "./Components/models/linkyoutube.gltf";
import Journal from "./Components/models/linkjournal.gltf";
//3d Models

const popUpScreen = "Navigate the 3D environment with your finger/cursor.  You can click on texts to reveal more information.";

function Home() {
  //3d Models---------------------------------------------------------------
  const room = useGLTFLoader(Room, true);
  const introduction = useGLTFLoader(Introduction, true);
  const featured = useGLTFLoader(Featured, true);
  const values = useGLTFLoader(Values, true);
  const interests = useGLTFLoader(Interests, true);
  const current = useGLTFLoader(Current, true);
  const instagram = useGLTFLoader(Instagram,true);
  const youtube = useGLTFLoader(Youtube,true);
  const journal = useGLTFLoader(Journal,true);
  

  function IntroductionRoute() {
    return (
      <mesh onPointerDown={() => history.push("/introduction")}>
        <primitive object={introduction.scene} position={[0, -3.8, 0]} />
      </mesh>
    );
  }
  function FeaturedRoute() {
    return (
      <mesh onPointerDown={() => history.push("/featured-projects")}>
        <primitive object={featured.scene} position={[0, -3.8, 0]} />
      </mesh>
    );
  }

  function ValuesRoute() {
    return (
      <mesh onPointerDown={() => history.push("/values")}>
        <primitive object={values.scene} position={[0, -3.8, 0]} />
      </mesh>
    );
  }

  function InterestsRoute() {
    return (
      <mesh onPointerDown={() => history.push("/personal-interests")}>
        <primitive object={interests.scene} position={[0, -3.8, 0]} />
      </mesh>
    );
  }

  function CurrentRoute() {
    return (
      <mesh onPointerDown={() => history.push("/current-project")}>
        <primitive object={current.scene} position={[0, -3.8, 0]} />
      </mesh>
    );
  }

  
  function InstagramRoute() {
    return (
      <mesh onPointerDown={() => history.push("/instagram")}>
        <primitive object={instagram.scene} position={[0, -3.8, 0]} />
      </mesh>
    );
  }

  
  function YoutubeRoute() {
    return (
      <mesh onPointerDown={() => history.push("/youtube")}>
        <primitive object={youtube.scene} position={[0, -3.8, 0]} />
      </mesh>
    );
  }

  
  function JournalRoute() {
    return (
      <mesh onPointerDown={() => history.push("/journal")}>
        <primitive object={journal.scene} position={[0, -3.8, 0]} />
      </mesh>
    );
  }
//3d Models---------------------------------------------------------------

const history = useHistory();
const [openModal, setopenModal] = useState(true);
const props = useSpring({opacity: 1, from: {opacity: 0}})
const props2 = useSpring({opacity: 0.8, from: {opacity: 1}})

function PopUp() {
  if (!openModal) return null
  return (
  <div className="background" style={props2}>
    <div className="PopUp">
      <div className="content">
      <h1 className="description">{popUpScreen}</h1>
      <animated.h1 className="exit" style={props} onClick={() => setopenModal(false)}>Start Exploring</animated.h1>
      </div>
    </div>
  </div>
  )
}

  return (
    
    <section className="App">
      <div className="navigation"></div>
      {/* <PopUp open={openModal} close={() => setopenModal(true)}/> */}
      <PopUp/>
      <Canvas
        colorManagement
        shadowMap
        camera={{ position: [0, -0, 2, 1], fov: 55, zoom: 1 }}
      >
        <mesh rotation={[0, 3.14, 0]}>
          <IntroductionRoute />
          <FeaturedRoute />
          <ValuesRoute/>
          <InterestsRoute/>
          <CurrentRoute/>
          <InstagramRoute/>
          <YoutubeRoute/>
          <JournalRoute/>
          {/* <Btn object={introduction.scene} route="/introduction"/> */}
          <primitive
            object={room.scene}
            position={[0, -3.8, 0]}
            dispose={null}
          />
        </mesh>
        <ambientLight intensity={1.6} />
        <OrbitControls
          enableRotate={true}
          enableZoom={false}
          maxPolarAngle={1.495}
          minPolarAngle={1.495}
          enablePan={false}
          screenSpacePanning={true}
          rotateSpeed={-0.6}
        />
      </Canvas>
  
    </section>
  );
}


function IntroductionModal() {
  return <Modal></Modal>;
}

function FeaturedModal() {
  return <Modal></Modal>;
}

function ValuesModal() {
  return <Modal></Modal>;
}


function InterestsModal() {
  return <Modal></Modal>;
}


function CurrentModal() {
  return <Modal></Modal>;
}


function App() {
  return (
    <BrowserRouter>
      <Route path="/" component={Home} />
      <Route path="/introduction" component={IntroductionModal} />
      <Route path="/featured-projects" component={FeaturedModal} />
      <Route path="/values" component={ValuesModal} />
      <Route path="/personal-interests" component={InterestsModal} />
      <Route path="/current-project" component={CurrentModal} />
      <Route path='/instagram' component={() => { 
      window.location.href = 'https://www.instagram.com/kazukisworld/'; 
      return null;}}/>
      <Route path='/youtube' component={() => { 
      window.location.href = 'https://www.youtube.com/channel/UCMVMsfpAXJJzri2MUuBtOhA'; 
      return null;}}/>
      <Route path='/journal' component={() => { 
      window.location.href = 'https://vocal.media/authors/kazuki-mori-243bf016a'; 
      return null;}}/>
    </BrowserRouter>
  );
}

export default App;
